<template>
  <button>
    <slot></slot>
    {{text}}
  </button>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      required: true,
    }
  },
}
</script>

<style scoped>
button {
  font-family: Inter;
  font-weight: bolder;
  border-radius: 10px;
  background-color:#c69e3d;
  border: solid transparent;
  cursor: pointer;
}
button:focus {
  outline: none;
}
</style>