<template>
    <div class="menu-container">
        <teleport to="body">
            <igoos-dialog-box @return-to-menu="returnToMenu" :product="selectedProduct" v-if="showDialog"></igoos-dialog-box>
        </teleport>
        <div class="menu-div">
            <span class="menu-span">
                Our Menu
            </span>
        </div>
        <div class="menu-items-grid">
            <span class="menu-items" v-for="product in products" :key="product.id">
                <img class="menu-items-image" :src="product.imageUrl" alt="">
                <h2>
                    {{product.item}}
                </h2>
                <igoos-button :text="product.price" class="add-to-cart-button" @click="setDialog(product)">
                    <svg class="add-to-bag-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"></path></svg>
                </igoos-button>
            </span>
        </div>
    </div>
</template>

<script>
import IgoosButton from "@/components/commons/igoosButton.vue";
import IgoosDialogBox from "@/components/commons/igoosDialogBox.vue";
import { getMenu } from "@/service/axios.js";
import { onMounted, ref } from 'vue';
export default {
    components: {
        IgoosButton,
        IgoosDialogBox,
    },
    setup() {
        const products = ref({});
        const showDialog = ref(false);
        const selectedProduct = ref({});
        onMounted(async () => {
            const res = await getMenu();
            products.value = res.data;
        });
        const setDialog = (product) => {
            selectedProduct.value = product;
            showDialog.value = true;
        };
        const returnToMenu = (value) => {
            showDialog.value = value;
        }
        return {
            products,
            selectedProduct,
            setDialog,
            showDialog,
            returnToMenu,
        }
    }
}
</script>

<style scoped>
h2 {
    font-size: 16px;
    margin-block: 0px;
    min-height: 48px;
    display: flex;
    align-items: center;
    margin-left: 1rem;
    margin-right: 1rem;
}
.menu-container {
    background: #e5e3d7;
    padding: 1rem;
    border-radius: 10px;
}
.menu-div {
    width: 100%;
    justify-content: center;
}
.menu-span {
    font-family: Quattrocento;
    font-weight: bolder;
    font-size: xxx-large;
    color: black;
    border-bottom: solid;
    padding-right: 5%;
    padding-left: 5%;
}
.menu-items-grid {
    display: grid;
    font-family: Quattrocento;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
    margin-top: 1rem;
}
.menu-items {
    margin-bottom: 1rem;
    display: grid;
    justify-items: center;
    font-weight: bold;
}
.menu-items-image {
    border: solid white 6px;
    width: 75%;
}
.add-to-bag-icon {
    height: 1rem;
    width: 1rem;
}
.add-to-cart-button {
    filter: drop-shadow(2px 2px 3px black);
    /* min-height: 39px */
}
</style>