<template>
    <div class="fade-screen">
        <div class="dialog-box">
            <h2>Select a set:</h2>
            <span class="dialog-choices"> 
                <igoos-button v-for="(option, n) in product.option" 
                :key="n" 
                :text="option.set"
                :class="{'unselected-quantity': selectedQuantity !== option.set}"
                class="button-default"
                @click="selectedOption(option)"
                ></igoos-button>
            </span>
            <h2>Quantity: </h2>
            <span class="quantity-input">
                <button 
                class="quantity-button"
                style="background: #c28888;"
                @click="quantityValue === 0 ? quantityValue : quantityValue -= 1"
                >
                    -
                </button>
                <input 
                type="number" 
                v-model="quantityValue"
                style="width:35%; textAlign: center; border: solid 1px #e5e3d7"
                disabled>
                <button 
                class="quantity-button"
                style="background: #92caa4;"
                @click="quantityValue += 1"
                >
                    +
                </button>
            </span>
            <h2 >Price:</h2>
            <h3>{{totalPrice}}</h3>
            <span class="final-button-container">
                <button 
                class="final-button" 
                style="borderTop: solid 1px #c69e3d"
                @click="$emit('return-to-menu', false)">
                    Cancel
                </button>
                <button 
                class="final-button" 
                style="background: #c69e3d">
                    Add to Cart
                </button>
            </span>
        </div>
    </div>
</template>

<script>
import IgoosButton from "@/components/commons/igoosButton.vue";
import { ref, watch } from 'vue';

export default {
    props: {
        product: {
            type: Object,
        }
    },
    components: {
        IgoosButton,
    },
    setup () {
        const selectedQuantity = ref(null);
        const quantityValue = ref(1);
        const priceValue = ref(0);
        const totalPrice = ref((0).toFixed(2));

        const computePrice = () => {
            totalPrice.value = (priceValue.value * quantityValue.value).toFixed(2);
        };
        const selectedOption = (product) => {
            const {set, price} = product;
            selectedQuantity.value = set;
            priceValue.value = price;
            computePrice();
        };

        watch(quantityValue, () => {
            computePrice();
        });

        return {
            selectedQuantity,
            quantityValue,
            totalPrice,
            selectedOption,

        }
    }
}
</script>

<style scoped>
h2 {
    font-family: Quattrocento;
    margin-left: 1rem;
    margin-right: 1rem;
}
h3 {
    font-family: Quattrocento;
    margin-left: 2rem;
    margin-right: 2rem;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.unselected-quantity {
    border: solid 2px #c69e3d;
    background: white;
}
.fade-screen {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    z-index: 1;
}
.dialog-box {
    position: relative;
    display: inline-block;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
    height: 50%;
    background: #e5e3d7;
    border-radius: 10px;
    overflow-y: auto;
}
.dialog-choices {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding-left: 1rem;
    padding-right: 1rem;
    text-align: center;  
}
.button-default {
    margin-left:4px;
    margin-right:4px;
    padding-top: 4px;
    padding-bottom: 4px;
}
.quantity-input {
    display: inline-block;
    text-align: center;
    margin-left: 1rem;
    margin-right: 1rem;
}
.quantity-button {
    width: 25%;
    background: white;
    border: none;
    cursor: pointer;
}
.final-button-container{
    width: 100%;
    display:flex;
    height: 15%;
    position: fixed;
    bottom: 0px;
}
.final-button{
    width: 100%;
    border: none;
    cursor: pointer;
}
</style>