<template>
<div>
    <igoos-button class="cart-button" @click="toggleOverview">
        <svg class="cart-icon" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path d="M3 1a1 1 0 000 2h1.22l.305 1.222a.997.997 0 00.01.042l1.358 5.43-.893.892C3.74 11.846 4.632 14 6.414 14H15a1 1 0 000-2H6.414l1-1H14a1 1 0 00.894-.553l3-6A1 1 0 0017 3H6.28l-.31-1.243A1 1 0 005 1H3zM16 16.5a1.5 1.5 0 11-3 0 1.5 1.5 0 013 0zM6.5 18a1.5 1.5 0 100-3 1.5 1.5 0 000 3z"></path></svg>
        <div class="cart-notification">
            {{quantityNotif}}
        </div>
    </igoos-button>
    <span class="cart-overview" v-show="showOverview">
        <h3>
            Total
        </h3>
        <h4>
            {{totalPrice.toFixed(2)}}
        </h4>
        <igoos-button style="borderRadius:0px" text="Go to Cart"></igoos-button>
    </span>
</div>
</template>

<script>
import IgoosButton from "@/components/commons/igoosButton.vue";
import { getCartNumber } from "@/service/axios.js";
import { onMounted, ref } from 'vue';

export default {
    components: {
        IgoosButton,
    },
    setup() {
        const cartContent = ref({});
        const quantityNotif = ref(0);
        const showOverview = ref(false);
        const totalPrice = ref(0);

        onMounted(async () => {
            const res = await getCartNumber();
            cartContent.value = res.data;
            quantityNotif.value = cartContent.value.length;
            
        });

        const getTotalPrice = () => {
            cartContent.value.map(item => {
                /* eslint-disable no-debugger */
            // debugger;
                totalPrice.value += Number(item.price);
            });
        }

        const toggleOverview = () => {
            getTotalPrice();
            showOverview.value = !showOverview.value;
            totalPrice.value = !showOverview.value ?  0 : totalPrice.value;
        }

        return {
            quantityNotif,
            toggleOverview,
            showOverview,
            totalPrice,
        }
    }
}
</script>

<style scoped>
h3 {
    margin-top: 4px;
    margin-bottom: 4px;
    font-family: Quattrocento;
}
h4 {
    margin-top: 4px;
    margin-bottom: 4px;
    font-family: Quattrocento;
}
.cart-button {
    background: #c69e3d;
    width: 65px;
    height: 65px;
    position: fixed;
    bottom: 20px;
    right: 20px;
    border-radius: 50%;
    border: solid 1px black;
    filter: drop-shadow(2px 2px 5px black);
}
.cart-icon {
    width: 30px;
    padding-top: 6px;
}
.cart-notification {
    position: fixed;
    top: 50%;
    left: 50%;
    background: #ff2020;
    border-radius: 50%;
    width: 19px;
    height: 20px;
    display: flex;
    justify-content: center;
    font-family: 'Quattrocento';
    font-weight: bolder;
    font-size: medium;
    align-items: center;
}
.cart-overview {
    position: fixed;
    bottom: 40px;
    right: 100px;
    width: 100px;
    height: 100px;
    background: #98968e;
    border-radius: 10px;
    display: grid;
    /* opacity: 0; */
}
.cart-overview::before {
    content: '';
    position: absolute;
    bottom: 0;
    right: -14px;
    z-index: -1;
    border: 20px solid;
    border-color: transparent transparent #c69e3d transparent;
}
.cart-overview-button {
    position: fixed;
}
</style>